import './VerticalTextOnTop.css';
import tumblr from '../images/tumblr-sm.png';
import twitter from '../images/twitter.png';
import vimeo from '../images/vimeo.png';

const VerticalTextOnTop = () => {
  return (
    <div className='vertical-text-on-top-relative'>
      <div className='vertical-text-on-top'>
        <h5 className='small-bold-font'>First Surfing Magazine</h5>
        <div className='vertical-text-on-top-icons'>
          <img
            src={tumblr}
            className='vertical-text-on-top-icon-smaller '
            alt=''
          />
          <img src={twitter} alt='' />
          <img src={vimeo} alt='' />
        </div>
      </div>
    </div>
  );
};

export default VerticalTextOnTop;
